@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@1,100&display=swap);
::-webkit-scrollbar{
  width: 1px;
  height: 1px;
  }
  ::-webkit-scrollbar-thumb{
  background: #f1faeeff;
  border-radius: 0px;
  }
  ::-webkit-scrollbar-thumb:hover{
  background: #B3AFB3;
  }
  ::-webkit-scrollbar-track{
  background: #f1faeeff;
  border-radius: 0px;
  box-shadow: inset 0px 0px 0px 0px #F0F0F0;
  }

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f1faeeff;
}

a{
  color: #e63946ff!important;
  text-decoration: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media only screen and (max-width: 992px) {
  /* body {
    overflow-x: visible;
  } */
}

.nav__desktop {
    display: flex;
    border: solid 1px #1d3557ff;
    -ms-writing-mode: tb-rl;
        writing-mode: vertical-rl;
    -webkit-text-orientation: upright;
            text-orientation: upright;
    margin: 0;
    position: absolute;
    top: 50%;
    right: 0%;
    transform: translateY(-50%);
    float: right;
    padding: 20px;
}

.nav__mobile {
    display: none;
}

.icons {
    padding: 20px;
    color: #1d3557ff;
}

.icons:hover {
    color: #e63946ff;
}

@media only screen and (max-width: 992px) {
    .nav__desktop{display: none;}

    .nav__mobile{
        display: flex;
        justify-content: space-between;
    }
}
.home__desktop {
    margin: 10%;
}

.home__mobile {display: none;}

.accent__1 {
    color: #1d3557ff;
}

.accent__2 {
    color: #e63946ff;
}

hr {
    margin-right: 10%;
    height: 2px;
    border: 0; 
    border-top: 2px solid #e63946ff;
}

h1 {
    font-size: 10vh;
}

p {
    font-size: 20px;
    font-weight: bold;
}

.name {
    font-size: 13vh;
    font-weight: bolder;
}

.social {
    display: flex;
    justify-content: space-between;
    margin-right: 10%;
    padding: 20px;
}

.emoji {
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    perspective: 1000vh;
}

@keyframes shake {
10%, 90% {
    transform: translate3d(-1px, 0, 0);
}

20%, 80% {
    transform: translate3d(2px, 0, 0);
}

30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
}

40%, 60% {
    transform: translate3d(4px, 0, 0);
}
}

@media only screen and (max-width: 992px) {
    .home__desktop{display: none;}

    .home__mobile{
        display: block;
        right: 50%;
        left: 20%;
        margin-top: 0;
        position: absolute;
        top: 25%;
        width: 50vw;
    }
    .home__mobile > .social {
        display: flex;
        justify-content: space-between;
        margin-right: 0%;
        padding: 0%;
        width: 50vw;
    }
    hr {
        margin-right: 0%;
    }
    h1 {
        font-size: 10vw;
    }
    .name {
        font-size: 13vw;
        font-weight: bolder;
    }
}
.about {
    margin: 10%;
    border: solid 1px #457B9D;
    padding: 20px;
    margin-right: 30%;
    box-shadow: 10px 20px  #A8DADC;
}

.accent__1 {
    color: #1d3557ff;
}

.accent__2 {
    color: #e63946ff;
}

h1 {
    font-size: 5vh;
}

@media only screen and (max-width: 992px) {
    .about{
        width: 70vw;
        box-shadow: none;
    }
    h1 {
        font-size: 7vw;
    }
}
.repos {
    display: flex;
    justify-content: space-between;
    margin: 10%;
    margin-right: 30%;
}

.repo_container {
    width: 100%;
    height: 30vh;
    border: solid 1px #A8DADC;
    margin-bottom: 10%;
}

.accent__1 {
    color: #1d3557ff;
}

.accent__2 {
    color: #e63946ff!important;
}

a{
    color: #e63946ff!important;
    text-decoration: none;
}

@media only screen and (max-width: 992px) {
    .repos {
        display: block;
        justify-content: space-between;
        margin: 10%;
    }
    .repo {
        padding-bottom: 20px;
    }
}
.contact {
    margin: 10%;
    width: 60vw;
}

.accent__1 {
    color: #1d3557ff;
}

.accent__2 {
    color: #e63946ff;
}

@media only screen and (max-width: 992px) {
    .contact{
        width: 80vw;
        box-shadow: none;
    }
    h1 {
        font-size: 7vw;
    }
}
.notFound {
    margin: 10%;
    border: solid 1px #457B9D;
    padding: 20px;
    margin-right: 30vw;
    box-shadow: 10px 20px  #A8DADC;
    text-align: center;
    margin-top: 40vh;
}

@media only screen and (max-width: 992px) {
    .notFound{
        width: 70vw;
        box-shadow: none;
    }
    h1 {
        font-size: 7vw;
    }
}
