.nav__desktop {
    display: flex;
    border: solid 1px #1d3557ff;
    writing-mode: vertical-rl;
    text-orientation: upright;
    margin: 0;
    position: absolute;
    top: 50%;
    right: 0%;
    transform: translateY(-50%);
    float: right;
    padding: 20px;
}

.nav__mobile {
    display: none;
}

.icons {
    padding: 20px;
    color: #1d3557ff;
}

.icons:hover {
    color: #e63946ff;
}

@media only screen and (max-width: 992px) {
    .nav__desktop{display: none;}

    .nav__mobile{
        display: flex;
        justify-content: space-between;
    }
}