.about {
    margin: 10%;
    border: solid 1px #457B9D;
    padding: 20px;
    margin-right: 30%;
    box-shadow: 10px 20px  #A8DADC;
}

.accent__1 {
    color: #1d3557ff;
}

.accent__2 {
    color: #e63946ff;
}

h1 {
    font-size: 5vh;
}

@media only screen and (max-width: 992px) {
    .about{
        width: 70vw;
        box-shadow: none;
    }
    h1 {
        font-size: 7vw;
    }
}