.repos {
    display: flex;
    justify-content: space-between;
    margin: 10%;
    margin-right: 30%;
}

.repo_container {
    width: 100%;
    height: 30vh;
    border: solid 1px #A8DADC;
    margin-bottom: 10%;
}

.accent__1 {
    color: #1d3557ff;
}

.accent__2 {
    color: #e63946ff!important;
}

a{
    color: #e63946ff!important;
    text-decoration: none;
}

@media only screen and (max-width: 992px) {
    .repos {
        display: block;
        justify-content: space-between;
        margin: 10%;
    }
    .repo {
        padding-bottom: 20px;
    }
}