.home__desktop {
    margin: 10%;
}

.home__mobile {display: none;}

.accent__1 {
    color: #1d3557ff;
}

.accent__2 {
    color: #e63946ff;
}

hr {
    margin-right: 10%;
    height: 2px;
    border: 0; 
    border-top: 2px solid #e63946ff;
}

h1 {
    font-size: 10vh;
}

p {
    font-size: 20px;
    font-weight: bold;
}

.name {
    font-size: 13vh;
    font-weight: bolder;
}

.social {
    display: flex;
    justify-content: space-between;
    margin-right: 10%;
    padding: 20px;
}

.emoji {
    animation: shake 0.82s cubic-bezier(.36,.07,.19,.97) both;
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden;
    perspective: 1000vh;
}

@keyframes shake {
10%, 90% {
    transform: translate3d(-1px, 0, 0);
}

20%, 80% {
    transform: translate3d(2px, 0, 0);
}

30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0);
}

40%, 60% {
    transform: translate3d(4px, 0, 0);
}
}

@media only screen and (max-width: 992px) {
    .home__desktop{display: none;}

    .home__mobile{
        display: block;
        right: 50%;
        left: 20%;
        margin-top: 0;
        position: absolute;
        top: 25%;
        width: 50vw;
    }
    .home__mobile > .social {
        display: flex;
        justify-content: space-between;
        margin-right: 0%;
        padding: 0%;
        width: 50vw;
    }
    hr {
        margin-right: 0%;
    }
    h1 {
        font-size: 10vw;
    }
    .name {
        font-size: 13vw;
        font-weight: bolder;
    }
}